  },
  methods: {
    scrollTo(section) {
      const element = document.getElementById(section.toLowerCase().replace(' ', '-'));
      if (element) {
        element.scrollIntoView({ behavior: 'smooth' });
      }
    },
    openFullscreenImage(imageUrl) {
      this.fullscreenImage = imageUrl;
    },
    
    closeFullscreenImage() {
      this.fullscreenImage = null;
    },
    showProjectDetails(project) {
      this.selectedProject = project;
    },
    closeProjectDetails() {
      this.selectedProject = null;
    },
    nextSlide() {
      this.currentSlide = (this.currentSlide + 1) % this.projects.length;
      this.highlightedImage = null;
    },
    prevSlide() {
      this.currentSlide = (this.currentSlide - 1 + this.projects.length) % this.projects.length;
      this.highlightedImage = null;
    },
    highlightImage(index) {
      if (this.highlightedImage === index) {
        this.highlightedImage = null;
      } else {
        this.highlightedImage = index;
      }
    }
  }
